<template>
    <el-form ref="form" :model="form" label-width="80px" class="form-contain">

        <el-form-item label="">
            <el-input v-model="form.name"></el-input>
        </el-form-item>

        <el-form-item label="">
            <el-input placeholder="" v-model="form.price" type='number'>
                <template slot="append"></template>
</el-input>
</el-form-item>


<el-form-item label="">
	<el-input-number v-model="form.inventory" :min="0"></el-input-number>
</el-form-item>

<el-form-item label="">
	<el-switch v-model="form.onsale"></el-switch>
</el-form-item>



 <el-form-item label="">
    <el-checkbox-group v-model="form.type">
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
    </el-checkbox-group>
  </el-form-item>



<el-form-item>
	<el-button type="primary" @click="onSubmit">{{isNew ? '' : ''}}</el-button>
	<el-button @click="onCancel"></el-button>
</el-form-item>

</el-form>
</template>

<script>
	export default {
		name: 'form',

		data() {
			return {
				isNew: 1, // 
				form: {
					id: undefined,
					name: '',
					onsale: 0,
					inventory: 0,
					price: 0,
					category: [],
					imgs: [],
					type: [],

				}
			}
		},
		methods: {
			onSubmit() {
				if (!this.form.name) {
					this.$message.warning('');
					return;
				}

				this.func.ajaxPost(this.api.goodsAdd, this.form, res => {
					if (res.data.code === 200) {
						this.$message.success('');
						this.$router.push('/admin/goods-list');
					}
				});
			},

			onCancel() {
				this.$router.push('/admin/goods-list');
			},

		},

		created() {
			let id = this.$route.query.id;

			if (id) {
				this.isNew = 0;

				this.func.ajaxPost(this.api.goodsDetail, {
					id
				}, res => {
					this.form = res.data.goods;
					this.form.id = res.data.goods.id;
				});
			}
		},

	}

</script>



