<template>
    <div class="admin-list">
		
		
<div class="crumbs">	
	<el-breadcrumb separator="/">
  <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
 
</el-breadcrumb>	
		
</div>	
		

		
<el-form :inline="true"  class="demo-form-inline">
  <el-form-item >
    <el-input v-model="select_word" placeholder=""></el-input>
  </el-form-item>
    <el-button type="primary" @click="search"></el-button>
  </el-form-item>
		
		
		<router-link to="/admin/user-form">
		<el-button type="success"></el-button>
	</router-link>
			
			  <el-button type="danger" @click="deleteMulti"></el-button>
</el-form>
		
        <el-table
            v-loading='load'
            ref="multipleTable"
            @selection-change="handleSelectionChange"
            :data="tableData"
            border
            tooltip-effect="dark"
            style="width: 100%">
            <el-table-column
                type="selection">
            </el-table-column>

            <el-table-column
                prop="user_name"
                label="">
            </el-table-column>

            <el-table-column
                width="160"
                label="">
                <template scope="scope">
                    <el-icon name="time"></el-icon>
                    <span style="margin-left: 10px">{{ scope.row.create_time }}</span>
                </template>
</el-table-column>

<el-table-column label="">
	<template scope="scope">
                    {{ scope.row.role }}
                </template>
</el-table-column>


<el-table-column label="">
	<template scope="scope">

                    <el-dropdown trigger="click" @command="changeRole">
                        <el-button size="small"
                                   @click='curRow = scope.row'>
                            <i class="el-icon-caret-bottom el-icon--right"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for='role in roles'
                                              :key='role.val'
                                              :command="role.val"
                                              :disabled="scope.row.role == role.txt">{{ role.txt }}
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>

                    <el-button
                        size="small"
                        type="danger"
                        @click="handleDelete(scope.row)">
                    </el-button>

                </template>
</el-table-column>

</el-table>
<!--
<div class="pagination">
	
	<el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="500">
	</el-pagination>
</div>
-->
</div>

</template>

<script>
	export default {
		name: 'list',
		data() {
			return {
				tableData: [],
				cur_page: 1,
				multipleSelection: [],
				select_word: '',
				is_search: false,


				roles: [{
						val: '1',
						txt: ''
					},
					{
						val: '10',
						txt: ''
					},
					{
						val: '100',
						txt: ''
					},
				],

				curRow: null,

				load: false, // loading
			}
		},

		methods: {
			fetchList() {
				this.load = true;

				this.func.ajaxGet(this.api.userList, res => {
					this.tableData = res.data.users;
					this.load = false;
				});
			},


			//
			handleCurrentChange(val) {
				this.cur_page = val;
				this.getData();
			},

			//
			search() {
				this.is_search = true;
			},

			// 
			handleDelete(row) {
				this.func.ajaxPost(this.api.userDelete, {
					id: row.id
				}, res => {
					if (res.data.code === 200) {
						let index = this.tableData.indexOf(row);
						this.tableData.splice(index, 1);
						this.$message.success('');
					}
				});
			},

			// 
			changeRole(val) {
				this.func.ajaxPost(this.api.userChangeRole, {
						change_role: val,
						id: this.curRow.id
					},
					res => {
						if (res.data.code === 200) {
							this.$message.success('');
							this.fetchList();
						}
					}
				);

			},

			deleteMulti() {
				let multi = this.multipleSelection
				let id = multi.map(el => {
					return el.id;
				});

				this.func.ajaxPost(this.api.userDeleteMulti, {
					id
				}, res => {
					if (res.data.code === 200) {
						this.$message.success('');
						multi.forEach(el => {
							let i = this.tableData.indexOf(el);
							this.tableData.splice(i, 1);
						});
					}
				});
			},

			handleSelectionChange(val) {
				this.multipleSelection = val;
			}
		},

		created() {
			this.fetchList();
		},


	}

</script>



