<template>
    <el-form ref="form" :model="form" label-width="80px" class="form-contain">
        <el-form-item label="">
            <el-input v-model="form.member_name"></el-input>
        </el-form-item>

        <el-form-item label="">
            <el-input v-model="form.member_phone"></el-input>
        </el-form-item>

		 <el-form-item label="">
            <el-input v-model="form.recommendation_code"></el-input>
        </el-form-item>
		
		
        <el-form-item label="">
            <el-radio-group v-model="form.membership_level">
                <el-radio label="1"></el-radio>
                <el-radio label="10"></el-radio>
                <el-radio label="100">VIP</el-radio>
            </el-radio-group>
        </el-form-item>

		
		 <el-form-item label="">
         		<el-input type="textarea" :rows="3"  placeholder="" v-model="form.remarks">
				</el-input>
		
        </el-form-item>
		
		
        <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button @click="onCancel"></el-button>
        </el-form-item>
    </el-form>
</template>

<script>
	export default {
		name: 'form',
		data() {
			return {
				form: {
					member_name: '',
					member_phone: '',
					remarks: '',
					recommendation_code: '',
					membership_level: 1,
				}
			}
		},
		methods: {
			onSubmit() {
				if (!this.form.member_name) {
					this.$message.warning('');
					return;
				}

				this.func.ajaxPost(this.api.memberAdd, this.form, res => {

					if (res.data.code === 200) {
						this.$message.success('');
						this.$router.push('/admin/member-list');
					}
				});
			},

			onCancel() {
				this.$router.push('/admin/member-list');
			},

		},


		created() {
			let id = this.$route.query.id;

			if (id) {
				this.isNew = 0;

				this.func.ajaxPost(this.api.memberDetail, {
					id
				}, res => {
					this.form = res.data.members;
					this.form.id = res.data.members.id;
				});
			}
		},


	}

</script>



