<template>

    <div class="admin-list">
<div style="margin-bottom:30px">	
	<el-breadcrumb separator="/">
  <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
 
</el-breadcrumb>	
		
	</div>		

		
<el-form :inline="true"  class="demo-form-inline">
  <el-form-item >
    <el-input v-model="member_phone" placeholder=""></el-input>
  </el-form-item>
    <el-button type="primary" @click="search"></el-button>
  </el-form-item>	
		<router-link to="/admin/member-form">
		<el-button type="success"></el-button>
	</router-link>
</el-form>
			
        <el-table
            v-loading='load'
            ref="multipleTable"
            @selection-change="handleSelectionChange"
            :data="tableData"
            border
            tooltip-effect="dark"
            style="width: 100%">
            <el-table-column
                type="selection">
            </el-table-column>

            <el-table-column
                prop="member_name"
                label="">
            </el-table-column>
	
	       <el-table-column
                prop="member_phone"
                label="">
            </el-table-column>
			
<!--
			   <el-table-column
                prop="recommendation_code"
                label="">
            </el-table-column>
			
-->

	<el-table-column prop="membership_level" label=""  :formatter="formatLevel" sortable>
			</el-table-column>
            <el-table-column sortable
                width="160"
                label="">
                <template scope="scope">
                    <el-icon name="time"></el-icon>
                    <span style="margin-left: 10px">{{ scope.row.create_time }}</span>
                </template>
</el-table-column>


<!--
        
   			<el-table-column
                prop="remarks"
                label="">
            </el-table-column>
-->

<el-table-column label="" width="300">
	<template scope="scope">
     <el-dropdown trigger="click" @command="changeRole">
                        <el-button size="small"
                                   @click='curRow = scope.row'>
                            <i class="el-icon-caret-bottom el-icon--right"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for='membership_level in membership_levels'
                                              :key='membership_level.val'
                                              :command="membership_level.val"
                                              :disabled="scope.row.membership_level == membership_level.txt">{{ membership_level.txt }}
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>

 <el-button
                        size="small"
                        @click="editMember(scope.row)">
                    </el-button>
                    <el-button
                        size="small"
                        type="danger"
                        @click="handleDelete(scope.row)">
                    </el-button>

                </template>
</el-table-column>

</el-table>
<div class="pagination">
	
	<el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="500">
	</el-pagination>
</div>

</div>
</template>

<script>
	export default {
		name: 'list',
		data() {
			return {
				tableData: [],

				cur_page: 1,
				member_phone: '',
				multipleSelection: [],

				membership_levels: [{
						val: '1',
						txt: ''
					},
					{
						val: '10',
						txt: ''
					},
					{
						val: '100',
						txt: 'VIP'
					},
				],

				curRow: null,

				load: false, // loading
			}
		},

		methods: {

			//
			formatLevel: function(row, column) {
				return row.membership_level == 1 ? '' : row.membership_level == 10 ? '' : row.membership_level == 100 ? 'VIP' : '';
			},

			//
			fetchList() {
				this.load = true;
				this.func.ajaxGet(this.api.memberList, res => {
					this.tableData = res.data.members;
					this.load = false;
				});
			},

			
			
			fetchList() {
		
			 this.load = true;
				var reqParams ={

					member_phone:this.member_phone,
					cur_page :this.cur_page,
				
				};
		
            this.func.ajaxPost(this.api.memberList,reqParams,res => {
                this.tableData = res.data.members;
                this.load = false;
            });
				
		
			},	
			
			
			
			

			//
			handleCurrentChange(val) {
				this.cur_page = val;
				this.fetchList();
			},

			//
			search() {
			
				this.fetchList();
			},


			// 
			editMember(row) {
				console.log(row);
				this.$router.push({
					path: '/admin/member-form',
					query: {
						id: row.id
					}
				});
			},



			// 
			handleDelete(row) {
				this.func.ajaxPost(this.api.memberDelete, {
					id: row.id
				}, res => {
					if (res.data.code === 200) {
						let index = this.tableData.indexOf(row);
						this.tableData.splice(index, 1);
						this.$message.success('');
					}
				});
			},

			// 
			changeRole(val) {
				this.func.ajaxPost(this.api.memberChangeRole, {
						change_role: val,
						id: this.curRow.id
					},
					res => {
						if (res.data.code === 200) {
							this.$message.success('');
							this.fetchList();
						}
					}
				);

			},

			//            deleteMulti () {
			//                let multi = this.multipleSelection
			//                let id = multi.map(el => {
			//                    return el.id;
			//                });
			//
			//                this.func.ajaxPost(this.api.memberDeleteMulti, {id}, res => {
			//                    if (res.data.code === 200) {
			//                        this.$message.success('');
			//                        multi.forEach(el => {
			//                            let i = this.tableData.indexOf(el);
			//                            this.tableData.splice(i, 1);
			//                        });
			//                    }
			//                });
			//            },

			handleSelectionChange(val) {
				this.multipleSelection = val;
			}
		},

		created() {
			this.fetchList();
		},


	}

</script>



